.guest-card {
    color:var(--primary-color);
    background:var(--light-color);
    margin:0.7rem 0;
    position:relative;
    max-width:26rem;
    line-height:1.6;
    margin-right:1rem
}

.card-head {
    display:flex;
    justify-content: space-between;
    align-items:baseline;
    background:#ccc;
    padding:0.2rem
}

.card-head input[type="checkbox"]{
    display:none
}

.card-head i, .card-head label {
    color:rgb(150, 150, 150);
    padding:0.1rem;
    cursor:pointer
}

.card-head i:hover{
    color:var(--primary-color);
    opacity:0.8;
    transition:all 0.1s ease 0s
}

.card-head .remove:hover {
    color:red;
    opacity:0.7;
    transition:all 0.1s ease 0s
}

.confirm {
    color:var(--primary-color) !important;
}

.card-head label {
    margin-right:0.2rem
}

.card-head button {
    border:none;
    padding:1.2rem;
    position:relative;
    display:flex;
    flex-direction:column
}

.card-head button:focus {
    outline:none
}

.card-body{
    padding:1.2rem;
    position:relative;
    display:flex;
    flex-direction:column
}
.card-body h2 {
    margin-top:1rem;
    padding:0
}



.contact p {
    font-weight:bold;
    padding:0.0.5rem
}

