.navbar {
    display:flex;
    justify-content: space-between;
    align-items:center;
    padding:0.7rem 2rem;
    z-index: 1;
    width:100%;
    color:var(--light-color);
    background-color:var(--primary-color);
    margin-bottom:1rem
}

.logo{
    display:flex;
    flex-direction:column;
    align-items:center
}

.logo p {
    font-size:0.9rem;
    margin-top:0.5rem;
    color:#ccc
}

.logo span {
    color:rgb(255, 57,22 )
}

.navbar ul {
    display:flex
}

.navbar i, h1 {
    padding:0 1rem
}


li{
    list-style:none;
    padding:0 1rem;
    transition:all 0.3s ease 0s
}

a{
    text-decoration:none;
    color:var(--light-color)
}

li i:hover , a:hover{
    opacity:0.5;
    cursor: pointer;
}

